<template>
  <el-row class="bgf">
      <el-col :span="12" :offset="5">
        <el-form label-width="100px" :model="forminfo" :rules="rules" ref="forminfo">
            <el-form-item label="类型" prop="type">
                <el-radio  :disabled="$route.params.id ? true:false"  v-model="forminfo.type" :label="1">目录</el-radio>
                <el-radio  :disabled="$route.params.id ? true:false" v-model="forminfo.type" :label="2">菜单</el-radio>
            </el-form-item>
            <el-form-item label="上级" prop="pid"  v-show="forminfo.type==2" >
                <el-select v-model="forminfo.pid" placeholder="请选择父级">
                    <el-option label="顶级目录" :value="0"></el-option>
                    <el-option v-for="item in menulist" :label="item.title" :value="item.id" :key="item.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="标题" prop="title">
                <el-input v-model="forminfo.title"></el-input>
            </el-form-item>
            <el-form-item label="地址" prop="url" v-show="forminfo.type==2">
                <el-input v-model="forminfo.url"></el-input>
            </el-form-item>
            <el-form-item label="图标" prop="icon"  v-show="forminfo.type==1">
                <el-input v-model="forminfo.icon"></el-input>
            </el-form-item>
            <el-form-item label="状态" prop="status">
                <el-radio v-model="forminfo.status" :label="1">正常</el-radio>
                <el-radio v-model="forminfo.status" :label="0">禁用</el-radio>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" v-if="!$route.params.id" @click="submit">立即添加</el-button>
                <el-button type="success" v-else @click="submit">立即修改</el-button>
                <el-button @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
     </el-col>   
  </el-row>
</template>

<script>
// 如何区分是添加还是修改页面！
    // 根据路由是否传入的 动态参数！  this.$route.params.id  修改！
import { getMenu,editMenu,addMenu } from "@/request/menu"
let defaultInfo = {};   // 修改的时候的重置！
export default {
    data(){
        return{
            forminfo:{
                pid:0,
                title:"",
                type:1,
                url:"",
                icon:"",
                status:1
            },
            rules:{ // 表单的验证规则
                title:[
                    { required:true,message:"标题必须写",trigger:'blur' }
                ]
            },
            menulist:[]   //菜单数据,用于下拉列表！
        }
    },
    created(){
        this.getlist();  // 获取列表
        if(this.$route.params.id){
            // 获取修改的这一条的数据，赋给表单！
            this.getinfo(this.$route.params.id)
        }
    },
    methods:{
        async getlist(){
            // 获取树形菜单数据！
            this.menulist = await getMenu();
        },
        async getinfo(id){
            // 获取修改数据，赋给表单！
            this.forminfo = await getMenu(id);
            // 赋给一个临时变量，用于重置的时候再次赋值！
            defaultInfo = { ...this.forminfo }
        },
        submit(){  // 提交数据 【添加、修改】
            this.$refs.forminfo.validate(async isok=>{
                if(isok){
                    // 发送请求！
                    let res;
                    if(this.$route.params.id){
                        this.forminfo.id = this.$route.params.id;   // 给表单数据添加上id
                        res = await editMenu(this.forminfo); // 修改请求
                    }else{
                        res = await addMenu(this.forminfo);  // 添加请求
                    }
                    if(res){
                      this.$router.back();
                    }
                }else{
                    this.$message.warn('验证不通过')
                }
            })
        },
        reset(){  // 重置
            // resetFields表单重置！
            if(this.$route.params.id){  // 修改重置
                this.forminfo = { ...defaultInfo }
            }else{ // 添加重置
                this.$refs.forminfo.resetFields();
            }
        }
    },
    components:{}
}

</script>
<style scoped>
</style>


